/**
 * RISIVO CRM - Demo Investor Login API
 * 
 * Allows verified demo investors to log in with their email and password
 */

import { NextResponse } from 'next/server'
import { cookies } from 'next/headers'
import { prisma } from '@/lib/db'
import { sendAdminNotification, getAdminDemoAccessedEmail } from '@/lib/email/sendgrid'
import bcrypt from 'bcryptjs'
import jwt from 'jsonwebtoken'

export const runtime = 'nodejs'
export const dynamic = 'force-dynamic'

export async function POST(request: Request) {
  console.log('[Demo Investor Login] Request received')
  
  try {
    const body = await request.json()
    const { email, password } = body

    if (!email || !password) {
      return NextResponse.json(
        { success: false, error: 'Email and password are required' },
        { status: 400 }
      )
    }

    const normalizedEmail = email.toLowerCase().trim()

    if (!prisma) {
      return NextResponse.json(
        { success: false, error: 'Database not available' },
        { status: 503 }
      )
    }

    // Find the demo access
    const demoAccess = await prisma.demoAccess.findUnique({
      where: { email: normalizedEmail },
    })

    if (!demoAccess) {
      return NextResponse.json(
        { success: false, error: 'Invalid email or password' },
        { status: 401 }
      )
    }

    // Check if email is verified
    if (!demoAccess.emailVerified) {
      return NextResponse.json(
        { success: false, error: 'Please verify your email first. Check your inbox for the verification link.' },
        { status: 403 }
      )
    }

    // Check if has password (self-signup users)
    if (!demoAccess.password) {
      return NextResponse.json(
        { success: false, error: 'This account was created by an admin. Please use the magic link sent to your email.' },
        { status: 403 }
      )
    }

    // Check status
    if (demoAccess.status === 'revoked') {
      return NextResponse.json(
        { success: false, error: 'Your demo access has been revoked. Please contact support.' },
        { status: 403 }
      )
    }

    if (demoAccess.status === 'expired' || new Date() > demoAccess.expiresAt) {
      return NextResponse.json(
        { success: false, error: 'Your demo access has expired. Please sign up again.' },
        { status: 403 }
      )
    }

    // Verify password
    const validPassword = await bcrypt.compare(password, demoAccess.password)
    if (!validPassword) {
      return NextResponse.json(
        { success: false, error: 'Invalid email or password' },
        { status: 401 }
      )
    }

    // Update usage tracking
    const updatedDemo = await prisma.demoAccess.update({
      where: { id: demoAccess.id },
      data: {
        lastAccessAt: new Date(),
        lastUsedAt: new Date(),
        accessCount: { increment: 1 },
        usageCount: { increment: 1 },
      },
    })

    // Send admin notification for demo access
    const isFirstAccess = demoAccess.accessCount === 0 // Before increment, so this login is the first
    const adminEmail = getAdminDemoAccessedEmail({
      name: demoAccess.name,
      email: demoAccess.email,
      company: demoAccess.company || undefined,
      accessTime: new Date(),
      accessCount: updatedDemo.accessCount,
      isFirstAccess,
    })
    
    try {
      const adminEmailSent = await sendAdminNotification(adminEmail)
      if (adminEmailSent) {
        console.log('[Demo Investor Login] Admin notification sent successfully')
      } else {
        console.error('[Demo Investor Login] Admin notification failed to send')
      }
    } catch (err) {
      console.error('[Demo Investor Login] Failed to send admin notification:', err)
    }

    // Create JWT
    const jwtSecret = process.env.NEXTAUTH_SECRET || process.env.AUTH_SECRET
    if (!jwtSecret) {
      return NextResponse.json(
        { success: false, error: 'Server configuration error' },
        { status: 500 }
      )
    }

    const jwtPayload = {
      id: `demo-${demoAccess.id}`,
      email: demoAccess.email,
      name: demoAccess.name,
      role: 'demo',
      isMasterAdmin: false,
      status: 'active',
      agencyId: null,
      agencyName: 'Demo',
      sub: `demo-${demoAccess.id}`,
    }

    const jwtToken = jwt.sign(jwtPayload, jwtSecret, { expiresIn: '7d' })

    console.log('[Demo Investor Login] Login successful for:', demoAccess.email)

    // Set cookies
    const cookieStore = await cookies()
    const isProduction = process.env.NODE_ENV === 'production'
    const cookieName = isProduction ? '__Secure-authjs.session-token' : 'authjs.session-token'

    cookieStore.set(cookieName, jwtToken, {
      httpOnly: true,
      secure: isProduction,
      sameSite: 'lax',
      path: '/',
      maxAge: 7 * 24 * 60 * 60,
    })

    cookieStore.set('demo_access_token', demoAccess.accessToken, {
      httpOnly: true,
      secure: isProduction,
      sameSite: 'lax',
      path: '/',
      maxAge: 7 * 24 * 60 * 60,
    })

    return NextResponse.json({
      success: true,
      user: {
        id: jwtPayload.id,
        email: demoAccess.email,
        name: demoAccess.name,
        company: demoAccess.company,
      },
    })

  } catch (error) {
    console.error('[Demo Investor Login] Error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to process login' },
      { status: 500 }
    )
  }
}
